<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card shadow="never">
      <!--  -->
      <el-row :gutter="20">
        <el-col :span="7">
          <el-input v-model="queryInfo.query" placeholder="" clearable>
            <el-button slot="append" icon="el-icon-search" @click="getGoodsList()"></el-button>
          </el-input>
        </el-col>
      </el-row>
      <!--  -->
      <el-table :data="ordersInfo.orderslist" border stripe>
        <el-table-column type="index" label="#"></el-table-column>
        <el-table-column label="" prop="order_number"></el-table-column>
        <el-table-column label="" prop="order_price" width="70px"></el-table-column>
        <el-table-column label="" prop="pay_status" align="center" width="80px">
            <template v-slot="scope"><el-tag :type="scope.row.pay_status==='1'?'success':'danger'">{{scope.row.pay_status==='1'?'':''}}</el-tag></template>
        </el-table-column>
        <el-table-column label="" prop="is_send" width="75px"></el-table-column>
        <!-- main.js -->
        <el-table-column label="" prop="create_time" width="150px">
          <template v-slot="scope">{{scope.row.create_time|dateFormat}}</template>
        </el-table-column>
        <el-table-column label="" width="120px">
          <template v-slot="scope">
            <!--  -->
            <el-tooltip effect="dark" content="" placement="bottom" :enterable="false">
              <el-button
                size="small"
                type="primary"
                icon="el-icon-edit"
                @click="showEditAddressInfoDialog(scope.row.order_id)"
              ></el-button>
            </el-tooltip>
            <!--  -->
            <el-tooltip effect="dark" content="" placement="bottom" :enterable="false">
              <el-button
                size="small"
                type="success"
                icon="el-icon-truck"
                @click="showProgressInfoDialog(scope.row.order_id)"
              ></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>
      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[1, 5, 10, 20]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="ordersInfo.total"
        background
      ></el-pagination>
    </el-card>
    <!--  -->
    <editAddressDialog :propisEditAddressDialogVisible.sync="isEditAddressDialogVisible" :orderId="editAddressId"></editAddressDialog>
    <!--  -->
    <showProgressDialog :propisShowProgressDialogVisible.sync="isShowProgressDialogVisible" :orderId="orderId"></showProgressDialog>
  </div>
</template>

<script>
import editAddressDialog from './dialog/editAddressDialog'
import showProgressDialog from './dialog/showProgressDialog'
export default {
    components:{editAddressDialog,showProgressDialog},
    data() {
    return {
      // 
      queryInfo: { query: "", pagenum: 1, pagesize: 5 }, //pagenum  pagesize 
      // 
      ordersInfo: { orderslist: [], total: 0 },
      //
      isEditAddressDialogVisible: false,
      //
      isShowProgressDialogVisible: false,
      // id,
      editAddressId: undefined,
      // ,
      orderId: undefined,
    };
  },
  created() {
    // 
    this.getOrdersList();
  },
  methods: {
    //
    async getOrdersList() {
        
      const { data: res } = await this.$axios.get("orders", {
        params: this.queryInfo,
      });
      //
      this.ordersInfo.orderslist = res.data.goods;
      //
      this.ordersInfo.total = res.data.total;
      if (res.meta.status !== 200) {
        return this.$message.error(res.meta.msg);
      }
    },
    // 
    handleSizeChange(newSize) {
      this.queryInfo.pagesize = newSize;
      this.getOrdersList();
    },
    // 
    handleCurrentChange(newPage) {
      this.queryInfo.pagenum = newPage;
      this.getOrdersList();
    },
    // id
    showEditAddressInfoDialog(addressId) {
      this.editAddressId = addressId;
      this.isEditAddressDialogVisible = true;
    },
    // 
    showProgressInfoDialog(orderId){
        this.orderId = orderId;
        this.isShowProgressDialogVisible = true;
    }

  },
  watch: {},
};
</script>